.button {
    padding: 15px 35px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    border: none;
    font-size: 1rem;
  }
  
  .primary {
    background-color: #0078D4;
    color: white;
  }
  
  .secondary {
    background-color: #25292E;
    color: white;
  }